<template>
    <template-for-get-pages
        title="Locations"
        link="services/locations/"
        :formatFields="{ number: '$L000000' }"
        :keys="keys"
    ></template-for-get-pages>
</template>
<script>
import TemplateForGetPages from './TemplateForGetPages';
export default {
    name: 'LocationsPage',   
    components: {
        TemplateForGetPages
    },
    data() {
        return {
            keys: [
                {
                    key: 'id',
                    sortable: true
                },
                {
                    key: 'number',
                    sortable: true
                },
                {
                    key: 'name',
                    sortable: true
                },
                {
                    key: 'address',
                    sortable: true
                },
                {
                    key: 'client__str',
                    label: 'Client',
                    sortable: true
                }
            ]
        };
    }
};
</script>
