<template>
    <div>
        <b-row>
            <b-col
                md="12"
                class="text-align-left"
            >
                <router-link
                    class="add-button mr-4"
                    :to="{ path: $router.currentRoute.path + '/add' }"
                >
                    <b-button
                        variant="primary"
                        class=""
                    >Create new</b-button>
                </router-link>
                <b-form-input
                    class="search-input"
                    id="searchInput"
                    placeholder="Type to search"
                    @input="onSearch"
                    v-model="search"
                ></b-form-input>
            </b-col>
        </b-row>
        <b-row class="white-card mt-3">
            <transition name="fade">
                <b-col cols="12">
                    <h3 class="page-title">{{ title }}</h3>
                    <div v-if="isLoading">
                        <h6>Loading</h6> <preloader size="40px"></preloader>
                    </div>
                    <b-table
                        v-if="!isLoading"
                        :show-empty="true"
                        :items="items"
                        :fields="fields"
                        responsive
                        :no-local-sorting="true"
                        @sort-changed="onSortChange"
                        :sort-by="sortBy"
                        :sort-desc="sortDesc"
                        class="table-for-get-pages"
                    >
                        <template slot="_delete" slot-scope="data">
                            <b-button
                                v-if="!isDeletingItems[data.item.id]"
                                variant="link"
                                class="py-0 delete-button"
                                @click="onDelete(data.item.id, data.item)"
                            >
                                <i class="fa fa-trash"></i>
                            </b-button>
                            <preloader v-if="isDeletingItems[data.item.id]" size="20px"></preloader>
                        </template>
                        <template slot="_edit" slot-scope="data">
                            <router-link
                                class="add-button"
                                :to="{ path: relativePath + data.item.id + '/edit' }"
                            >
                                <b-button
                                    variant="link"
                                    class="py-0 edit-button fa fa-edit"
                                ></b-button>
                            </router-link>
                        </template>
                    </b-table>
                    <router-view></router-view>
                </b-col>
            </transition>
        </b-row>
        <b-pagination-nav
            v-if="!isLoading && numberOfPages > 1"
            :link-gen="paginationLinkGen"
            :number-of-pages="numberOfPages"
            v-model="currentPage"
            class="pagination"
        />

    </div>
</template>
<script>
import Backend from '../../services/backend';
import Preloader from '../Preloader';
import { debounce } from 'lodash';
import StringMask from 'string-mask';

const backend = new Backend();

export default {
    name: 'TemplateForGetPages',
    data() {
        return {
            items: [],
            isDeletingItems: {},
            isLoading: false,
            fields: [],
            numberOfPages: null,
            currentPage: null,
            sortBy: null,
            sortDesc: null,
            search: this.$route.query.search
        };
    },
    components: {
        Preloader
    },
    props: [
        'title',
        'link',
        'formatFields',
        'keys'
    ],
    watch: {
        '$route.query': 'loadValues'
    },
    computed: {
        relativePath() {
            return this._props.link.replace(/[^/]*\//, '');
        }
    },
    created() {
        this.loadValues();
    },
    methods: {
        onSortChange(ctx) {
            let currentName = this.$route.name;
            let search = this.$route.query.search;
            this.sortBy = ctx.sortBy;
            this.sortDesc = ctx.sortDesc;
            this.$router.push({ name: currentName, query: { search, sortBy: ctx.sortBy, sortDesc: ctx.sortDesc } });
        },
        onSearch: debounce(function(value) {
            let newRoute = {
                name: this.$route.name,
                query: {}
            };
            if (value) {
                newRoute.query.search = value;
            }
            this.$router.push(newRoute);
        }, 600),
        onDelete(id, item) {
            let name = item.name || item.value || item.number || 'it';
            if (!window.confirm('Do you really want to delete ' + name + '?')) {
                return;
            }
            this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: true });
            backend.deleteRequest(this._props.link + id)
                .then(() => {
                    this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
                    this.loadValues();
                })
                .catch(this.onDeleteError(id));
        },
        loadValues() {
            this.isLoading = true;
            let sortDirection = !this.$router.currentRoute.query.sortDesc ? '' : '-';
            let sortBy = this.$router.currentRoute.query.sortBy;
            let search = this.$router.currentRoute.query.search;
            sortBy = sortBy && sortBy.replace(/__str$/, '');
            this.currentPage = this.$route.query.page ? parseInt(this.$route.query.page) : 1;
            let params = {
                page: this.currentPage,
                ordering: sortDirection + sortBy,
                search: search
            };
            backend.getRequest(this._props.link, params)
                .then(response => {
                    let keys = this._props.keys || Object.keys(response.data.results[0]);
                    this.fields = keys.concat('_edit', '_delete');
                    if (this.formatFields) {
                        let formatter = new StringMask(this.formatFields.number, { reverse: true });
                        this.items = response.data.results.map(item => {
                            item.number = formatter.apply(item.number).toUpperCase();
                            return item;
                        });
                    } else {
                        this.items = response.data.results;
                    }
                    this.items = response.data.results;
                    this.numberOfPages = response.data.num_pages;
                    this.isLoading = false;
                })
                .catch(error => {
                    if (error.response.status === 401) {
                        localStorage.removeItem('token');
                        this.$router.push({ name: 'login' });
                    }
                    this.isLoading = false;
                });
        },
        onDeleteError(id) {
            return (error) => {
                let errorText = error.response.data.error || 'Something went wrong. Please try again later';
                this.$store.dispatch('addNotification', { text: errorText, variant: 'danger' });
                this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
            };
        },
        paginationLinkGen(pageNum) {
            let newRoute = {
                name: this.$route.name,
                query: {
                    page: pageNum
                }
            };
            if (this.$route.query.sortBy) {
                newRoute.query.sortBy = this.$route.query.sortBy;
            }
            if (this.$route.query.sortDesc) {
                newRoute.query.sortDesc = this.$route.query.sortDesc;
            }
            if (this.$route.query.search) {
                newRoute.query.search = this.$route.query.search;
            }
            return newRoute;
        }
    }
};
</script>

<style scoped>
    .fade-enter-active, .fade-leave-active {
        transition: opacity ease-in-out 0.3s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }

    .delete-button {
        color: #dc3545;
        line-height: 0;
    }
    .edit-button {
        color: #6c757d;
        line-height: 0;
    }
</style>

<style>
    .table-for-get-pages th:nth-last-child(-n+2),
    .table-for-get-pages td:nth-last-child(-n+2) {
        padding-left: 15px;
        padding-right: 15px;
        width: 20px;
    }
    .pagination {
        margin-top: 6px;
        justify-content: center;
    }
    .search-input {
        display: inline-block;
        width: 250px;
        position: relative;
        top: 1px;
    }
    @media screen and (max-width: 438px) {
        .add-button button {
            margin-bottom: 10px;
            width: 100%;
        }
        .search-input {
            width: 100% !important;
            text-align: center;
        }
    }
</style>
