<template>
    <div class="white-card px-4 py-4">
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'circuits' }"
            >
                Circuits /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
        <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <b-form-group label="Location" label-for="inputLocation" v-if="!errorFetching">
                <b-form-select :options="locations"
                               id="inputLocation"
                               v-model="$v.form.location.$model"
                               :class="{ 'is-invalid': $v.form.location.$error }"
                               @change="onLocationChange"
                ></b-form-select>
                <b-form-invalid-feedback>
                    This field is required
                </b-form-invalid-feedback>
            </b-form-group>
            <transition name="fade">
                <b-form-group label="Device" label-for="inputDevice" v-if="devices && devices.length">
                    <b-form-select
                        id="inputDevice"
                        :options="devices"
                        v-model="$v.form.device.$model"
                        :class="{ 'is-invalid': $v.form.device.$error }"
                    ></b-form-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
            </transition>
            <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
            <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
        </b-form>
    </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
const backend = new Backend();

export default {
    name: 'CircuitLinksAddEditPage',
    components: {
        Preloader
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            locations: [],
            devices: [],
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id',
        'subId'
    ],
    validations: {
        form: {
            location: {
                required
            },
            device: {

            }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Circuit Links' : 'Add Circuit Links';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                let subId = this.$route.params.subId;
                backend.getRequest('services/circuits/' + id + '/links/' + subId)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                        if (this.form.location) {
                            this.onLocationChange(this.form.location);
                        }
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
            backend.getRequest('services/locations')
                .then((response) => {
                    this.locations = response.data.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.fetchErrorHandler);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = this.form;
            this.isSaving = true;
            let id = this.$route.params.id;
            let subId = this.$route.params.subId;
            if (this._props.pageType === 'add') {
                backend.postRequest('services/circuits/' + id + '/links/', data)
                    .then(() => {
                        this.errorMessage = null;
                        this.successMessage = `Circuit link was added successfully`;
                        this.isSaving = false;
                        this.clearForm();
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/circuits/' + id + '/links/' + subId + '/', data)
                    .then(() => {
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `Circuit link was changed successfully`;
                    })
                    .catch(this.onSaveError);
            }
        },
        onLocationChange(id) {
            this.form.device = '';
            backend.getRequest('services/locations/' + id + '/devices/')
                .then((response) => {
                    this.devices = response.data.map(item => {
                        return {
                            value: item.id,
                            text: item.serial_number
                        };
                    });
                })
                .catch(this.fetchErrorHandler);
        },
        onSaveError(error) {
            this.isSaving = false;
            this.successMessage = null;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        onFetchingError(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        clearForm() {
            this.form = {
                location: null,
                device: null
            };
            setTimeout(() => this.$v.$reset(), 0);
        },
        fetchErrorHandler(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response.data.error ||
                'Something went wrong. Please try again later';
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
