<template>
    <div class="white-card px-4 py-4">
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'circuits' }"
            >
                Circuits /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
          <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Number" label-for="inputNumber">
                    <b-form-input
                        v-model="$v.form.number.$model"
                        id="inputNumber"
                        label="Number"
                        type="number"
                        v-bind:class="{ 'is-invalid': $v.form.number.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        This field must contain maximum 6 digits
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Circuit type" label-for="inputCircuitType" class="mb-0">
                    <b-form-select
                        v-model="$v.form.circuit_type.$model"
                        id="inputCircuitType"
                        :options="circuitTypes"
                        @change="onCircuitTypeChange"
                        class="mb-3"
                        v-bind:class="{ 'is-invalid': $v.form.circuit_type.$error }"/>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <transition name="fade">
                    <b-form-group
                        v-if="circuitSubTypes && circuitSubTypes.length"
                        label="Circuit subtype"
                        label-for="inputCircuitSubType"
                        class="mb-0"
                    >
                        <b-form-select
                            v-model="$v.form.circuit_subtype.$model"
                            id="inputCircuitSubType"
                            :options="circuitSubTypes"
                            class="mb-3"
                            v-bind:class="{ 'is-invalid': $v.form.circuit_subtype.$error }"/>
                        <b-form-invalid-feedback>
                            This field is required
                        </b-form-invalid-feedback>
                    </b-form-group>
                </transition>
                <b-form-group label="Circuit speed" label-for="inputCircuitSpeed" class="mb-0">
                    <b-form-select
                        v-model="$v.form.circuit_speed.$model"
                        id="inputCircuitSpeed"
                        :options="circuitSpeeds"
                        class="mb-3"
                        v-bind:class="{ 'is-invalid': $v.form.circuit_speed.$error }"/>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Carrier" label-for="inputCarrier" class="mb-0">
                    <b-form-select
                        v-model="$v.form.carrier.$model"
                        id="inputCarrier"
                        :options="carriers"
                        class="mb-3"
                        v-bind:class="{ 'is-invalid': $v.form.carrier.$error }"/>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Carrier Vlan" label-for="inputCarrierVlan">
                    <b-form-input
                        v-model="$v.form.carrier_vlan.$model"
                        id="inputCarrierVlan"
                        v-bind:class="{ 'is-invalid': $v.form.carrier_vlan.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Carrier Service" label-for="inputCarrierService">
                    <b-form-input
                        v-model="$v.form.carrier_service.$model"
                        id="inputCarrierService"
                        v-bind:class="{ 'is-invalid': $v.form.carrier_service.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Installed on" label-for="inputInstalledOn">
                    <date-picker
                        v-model="$v.form.installed_on.$model"
                        lang="en"
                        format="YYYY-MM-DD"
                    ></date-picker>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <!--Links-->
                <div v-if="hasLinks && !isFetching">
                    <h4>Links</h4>
                    <b-row>
                        <b-col md="6">
                            <div class="link-point">
                                <span>Start point </span>
                                <i class="fa fa-map-marker"></i>
                            </div>
                            <b-form-group label="Location-A" label-for="selectLocationA">
                                <b-select
                                    v-model="$v.linksForm.locationA.$model"
                                    id="selectLocationA"
                                    :options="locations"
                                    :class="{ 'is-invalid': $v.linksForm.locationA.$error }"
                                    @change="onLocationAChange"
                                ></b-select>
                                    <!-- :state="$v.linksForm.locationA.$error" -->
                                <b-form-invalid-feedback>
                                    This field is required
                                </b-form-invalid-feedback>
                            </b-form-group>
                            <b-form-group label="Device" label-for="selectDeviceA">
                                <b-select
                                    v-model="$v.linksForm.deviceA.$model"
                                    id="selectDeviceA"
                                    :options="locationADevices"
                                    :class="{ 'is-invalid': $v.linksForm.deviceA.$error }"
                                ></b-select>
                                <b-form-invalid-feedback>
                                    This field is required
                                </b-form-invalid-feedback>
                            </b-form-group>
                        </b-col>
                        <b-col md="6">
                            <div class="link-point">
                                <span>End point </span>
                                <i class="fa fa-map-marker"></i>
                            </div>
                            <b-form-group label="Location-B" label-for="selectLocationB">
                                <b-select
                                    v-model="$v.linksForm.locationB.$model"
                                    id="selectLocationB"
                                    :options="locations"
                                    :class="{ 'is-invalid': $v.linksForm.locationB.$error }"
                                    @change="onLocationBChange"
                                ></b-select>
                                <b-form-invalid-feedback>
                                    This field is required
                                </b-form-invalid-feedback>
                            </b-form-group>
                            <b-form-group label="Device" label-for="selectDeviceB">
                                <b-select
                                    v-model="$v.linksForm.deviceB.$model"
                                    id="selectDeviceB"
                                    :options="locationBDevices"
                                    :class="{ 'is-invalid': $v.linksForm.deviceB.$error }"
                                ></b-select>
                                <b-form-invalid-feedback>
                                    This field is required
                                </b-form-invalid-feedback>
                            </b-form-group>
                        </b-col>
                    </b-row>
                </div>
                <b-button v-else variant="success" @click="hasLinks=true" class="mr-2">
                    <span class="fa fa-plus-circle"></span>
                    Add links
                </b-button>

                <b-button v-if="hasLinks" variant="danger" @click="hasLinks=false" class="mr-2">
                    <span class="fa fa-trash"></span>
                    Delete Links
                </b-button>

                 <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
                <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
            </div>
        </b-form>
    </div>
</template>

<script>
import { required, numeric, maxLength } from 'vuelidate/lib/validators';
import DatePicker from 'vue2-datepicker';
import moment from 'moment';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
import StringMask from 'string-mask';
const backend = new Backend();

export default {
    name: 'CircuitsAddEditPage',
    components: {
        Preloader,
        DatePicker
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            linksForm: {},
            title: '',
            circuitTypes: [],
            circuitSpeeds: [],
            circuitSubTypes: [],
            carriers: [],
            hasLinks: false,
            locations: [],
            locationADevices: [],
            locationBDevices: [],
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id'
    ],
    validations: {
        form: {
            number: {
                numeric,
                maxLength: maxLength(6)
            },
            circuit_type: {
                required
            },
            circuit_subtype: {

            },
            circuit_speed: {

            },
            carrier: {

            },
            carrier_vlan: {

            },
            carrier_service: {

            },
            installed_on: {

            }
           
        },
        linksForm: {
            locationA: {
                required
            },
            locationB: {
                required
            },
            deviceA: {
            },
            deviceB: {

            }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Circuits Edit Page' : 'Circuits Add Page';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/circuits/' + id)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch(this.onFetchingError);

                // getCircuitsLocations
                backend.getRequest('services/circuits/' + id + '/links/')
                    .then((response) => {
                        //  GetLocationA
                        let links = response.data; 
                        if (links.length === 0) {
                            this.clearLinksForm();
                        }
                        if (links[0]) {
                            let locationA = links[0].location;
                            this.hasLinks = true;
                            this.linksForm.linkAId = links[0].id;
                            this.linksForm.locationA = links[0].location;
                            this.linksForm.deviceA = links[0].device;
                            this.getLocationDevices(locationA, 'locationADevices');
                        }
                        // Get LocationB
                        if (links[1]) {
                            let locationB = links[1].location;
                            this.linksForm.linkBId = links[1].id;
                            this.linksForm.locationB = locationB;
                            this.linksForm.deviceB = links[1].device;
                            this.getLocationDevices(locationB, 'locationBDevices');
                        }
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.clearLinksForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
            backend.getRequest('services/circuit_types/')
                .then((response) => {
                    this.circuitTypes = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
            backend.getRequest('services/circuit_speeds/')
                .then((response) => {
                    this.circuitSpeeds = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.value
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
            backend.getRequest('services/carriers/')
                .then((response) => {
                    this.carriers = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
            backend.getRequest('services/locations/') 
                .then((response) => {
                    let formatter = new StringMask('$L000000', { reverse: true });
                    this.locations = response.data.results.map((item) => {
                        return {
                            value: item.id,
                            text: formatter.apply(item.number).toUpperCase() + ' | ' + item.name
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.form.$invalid) {
                this.$v.form.$touch();
                return;
            }
            if (this.hasLinks && this.$v.linksForm.$invalid) {
                this.$v.linksForm.$touch();
                return;
            }
            let data = Object.assign({}, this.form);
            if (!data.number) {
                delete data.number;
            }
            if (typeof data.installed_on === 'object' && data.installed_on !== null) {
                data.installed_on = moment(data.installed_on).format('YYYY-MM-DD');
            }

            var linkA = { 
                id: this.linksForm.linkAId,
                location: this.linksForm.locationA, 
                device: this.linksForm.deviceA 
            };
            var linkB = { 
                id: this.linksForm.linkBId,
                location: this.linksForm.locationB, 
                device: this.linksForm.deviceB 
            };

            this.isSaving = true;
            if (this._props.pageType === 'add') {
                backend.postRequest('services/circuits/', data)
                    .then((response) => {
                        let name = response.data.number;

                        if (this.hasLinks) {
                            backend.postRequest('services/circuits/' + response.data.id + '/links/', linkA)
                                .then((response) => {
                                    this.linksForm.linkAId = response.data.id;
                                })
                                .catch((error) => {
                                    console.log(error.response);
                                });
                            backend.postRequest('services/circuits/' + response.data.id + '/links/', linkB)
                                .then((response) => {
                                    this.linksForm.linkAId = response.data.id;
                                })
                                .catch((error) => {
                                    console.log(error.response);
                                });
                        }
                        this.errorMessage = null;
                        this.successMessage = `Circuit number ${name} was added successfully`;
                        this.isSaving = false;
                        this.clearForm();
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/circuits/' + this.$route.params.id + '/', data)
                    .then((response) => {
                        this.form.number = response.data.number;
                        if (this.hasLinks) {
                            if (linkA.id) {
                                backend.putRequest('services/circuits/' + response.data.id + '/links/' + linkA.id, linkA)
                                    .then(() => {
                                        console.log('successPutA');
                                    })
                                    .catch((error) => {
                                        console.log(error.response);
                                    });
                            } else {
                                backend.postRequest('services/circuits/' + response.data.id + '/links/', linkA)
                                    .then((response) => {
                                        this.linksForm.linkAId = response.data.id;
                                    })
                                    .catch((error) => {
                                        console.log(error.response);
                                    });
                            }
                            if (linkB.id) {
                                backend.putRequest('services/circuits/' + response.data.id + '/links/' + linkB.id, linkB)
                                    .then(() => {
                                    })
                                    .catch((error) => {
                                        console.log(error.response);
                                    });
                            } else {
                                backend.postRequest('services/circuits/' + response.data.id + '/links/', linkB)
                                    .then((response) => {
                                        this.linksForm.linkBId = response.data.id;
                                    })
                                    .catch((error) => {
                                        console.log(error.response);
                                    });
                            }
                        } else {
                            if (linkA.id) {
                                backend.deleteRequest('services/circuits/' + response.data.id + '/links/' + linkA.id)
                                    .then(() => {
                                        this.linksForm();
                                    })
                                    .catch((error) => {
                                        console.log(error.response);
                                    });
                            }
                            if (linkB.id) {
                                backend.deleteRequest('services/circuits/' + response.data.id + '/links/' + linkB.id)
                                    .then(() => {
                                        this.clearLinksForm();
                                    })
                                    .catch((error) => {
                                        console.log(error.response);
                                    });
                            }
                        }
                        let name = response.data.number;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `Circuit number ${name} was changed successfully`;
                    })
                    .catch(this.onSaveError);
            }
        },
        getLocationDevices(locationId, locationPointDevices) {
            backend.getRequest('services/locations/' + locationId + '/devices/')
                .then((response) => {
                    let formatter = new StringMask('D000000', { reverse: true });
                    this[locationPointDevices] = response.data.map((item) => {
                        return {
                            value: item.id,
                            text: formatter.apply(item.number) + '|' + item.device_model + '|' + item.serial_number
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        onCircuitTypeChange(id) {
            this.form.circuit_subtype = '';
            backend.getRequest('services/circuit_types/' + id + '/circuit_subtypes/')
                .then((response) => {
                    this.circuitSubTypes = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        onLocationAChange(id) {
            this.getLocationDevices(id, 'locationADevices');
            this.linksForm.deviceA = null;
        },
        onLocationBChange(id) {
            this.getLocationDevices(id, 'locationBDevices');
            this.linksForm.deviceB = null;
        },
        onSaveError(error) {
            this.isSaving = false;
            this.successMessage = null;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        onFetchingError(error) {
            console.log(error);
            console.log('ass');
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        clearForm() {
            this.form = {
                number: '',
                circuit_type: '',
                circuit_subtype: '',
                circuit_speed: '',
                carrier: '',
                carrier_vlan: '',
                carrier_service: '',
                installed_on: null
            };
            this.clearLinksForm();
            setTimeout(() => this.$v.$reset(), 0);
        },
        clearLinksForm() {
            this.linksForm = {
                linkAId: null,
                linkBId: null,
                locationA: null,
                locationB: null,
                deviceA: null,
                deviceB: null
            };
            this.hasLinks = false;
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
    .link-point {
        font-size: 17px;
        font-weight: 700;
        margin-bottom: 10px;
    }
    .link-point span {
        text-transform: uppercase;
    }
    .link-point i {
        font-size: 24px;
        color: #28a745;
    }
</style>
